import mInput from '@/components/m-input.vue';
import mList from '@/components/m-list.vue';
import service from '../../static/service/service.js';

export default {
	components: {
		mList,
		mInput
	},
	data() {
		return {
			// 请求对象
			params: {
				'tableId': "010401",
				'pageNumber': 1,
				'pageSize': 10,
				'queryKey': "搜索"
			},
			isLoadMore: true,
			dataList: [],
			// 用户输入的内容
			inputContent: '',
			radioList:[
				{
					value:'0',
					name:'全部提交',
					checked: 'true'
				},{
					value:'1',
					name:'部分提交'
				},
			],
			current: 0,
			// 提交页面的属性
			isHUDShow:true
		}
	},

	// 进入页面后刷新数据
	onShow() {
		this.params.pageNumber = 1
		this.params.queryKey = ""
		this.initData(0)
	},

	// 下拉刷新
	onPullDownRefresh() {
		this.params.pageNumber = 1
		this.initData(0)
		this.isLoadMore = true
		uni.showToast({
			'icon': 'none',
			'title': '刷新成功'
		})
		uni.stopPullDownRefresh()
	},

	// 加载更多数据
	onReachBottom() {
		if (this.isLoadMore) {
			this.params.pageNumber++
			this.initData(0)
		}
	},
	methods: {
		async initData(type) {
			// type==0 列表刷新 params不包含queryKey 
			// type==1 搜索刷新 params包含queryKey 
			if (type == 0) {
				let res = await service.taskList(this.params)
				if (res.errno == 0) {
					if (this.params.pageNumber == 1) {
						this.dataList = res.data.data;
					} else {
						this.dataList = this.dataList.concat(res.data.data);
					}
					if (res.data.data.length < 10) {
						this.isLoadMore = false;
					}
				}
			} else {
				let res = await service.taskListSearch(this.params)
				if (res.errno == 0) {
					if (this.params.pageNumber == 1) {
						this.dataList = res.data.data;
					} else {
						this.dataList = this.dataList.concat(res.data.data);
					}
					if (res.data.data.length < 10) {
						this.isLoadMore = false;
					}
				}
			}
		},
		// 用户搜索后刷新数据
		listingValueChange() {
			this.params.pageNumber = 1
			this.params.queryKey = this.inputContent
			this.initData(1)
		},
		// 点击提交任务,弹出选择提交类型页面
		handleSubmitTask(row) {
			this.isHUDShow = !this.isHUDShow;
		},
		// 隐藏提交类型页面
		handleHiddenHUD() {
			console.log('隐藏提交类型页面');
			this.isHUDShow = !this.isHUDShow;
		},
		// 点击radio后方法
		radioChange: function(evt) {
			console.log(evt);
			for (let i = 0; i < this.radioList.length; i++) {
				if (this.radioList[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
			return false;
		}
	},
	onLoad() {
		this.isHUDShow = !this.isHUDShow;
	}
}
